} */

/* .accordion{
  background-color: transparent;
} */

.taskContainer{
  margin: 0 1rem 0 1rem;
  display: flex;
  background-color: transparent;
}

.accordion-header{
  width: 100%;
}

.accordion-button{
  width: 100%;
  display: flex;
  background-color: transparent;  
}

.commitItem{
  display: flex;
  flex-direction: column;
  margin-left: 0.4rem;
  word-wrap: break-word;
}

.taskParagraph{
  margin: 0;
  padding: 0;
}

.dateParagraph{
  margin: 0;
  padding: 0;
}


.accordion-button{
  width: 95%;
}

.description{
  border: none;
  border-radius: 5px 5px 0 5px;
}

.editIcons{
  margin-left: 1rem;
  color: #282c34;
}

.accordion-body{
  display: flex;
}


.description{
  margin-left: 1.8rem;
  padding: 0 0 0 0.4rem;
  width: 84%;
}

.displayInline{
  display: inline;
}

.titleStyle{
  z-index: -1;
  width: 100%;
  border: none;
  background-color: #282c34;
  color: white;
  padding: 0.3rem 0 0.2rem 0;
  position: fixed;
  top: 0;
  padding: 0.5rem 0rem 0rem 0rem;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.5);
}

.taskList{
  margin-top: 9rem;
}

.taskInput{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  position: fixed;
  bottom: 0px;
  background-color: #d4d4d4;
  padding: 0rem 0rem 1rem 0rem;
  box-shadow: 0 -2px 20px rgba(0, 0, 0, 0.185);
}

.commitInputStyle{
  width: 65%;
  border: none;
  border-radius: 5px 0 0 5px;
  margin: 1rem 0 0 0;
  padding: 0.3rem;
}

.commitButtonStyle{
  margin: 1rem 0 0 0;
  background-color: #282c34;
  color: white;
  border: none;
  border-radius: 0 5px 5px 0;
  padding: 0.3rem 0.7rem 0.3rem 0.7rem;
}

.dateParagraph{
  color: #434d63;
  font-size:x-small;
  margin: 0 0 0.3rem 0;
}


.chekboxStyle{
  margin: 0.55rem 0.3rem 0 0;
  height: 1.5rem;
  width: 1.5rem;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
